export default {
	// 监听页面加载
	onLoad: function(option) {
		uni.showLoading({
			title: "正在加载"
		});
		let what = option.what;
		let id = option.id;
		let name = option.name;
		var that = this;
		uni.request({
			url: " http://127.0.0.1:3389/remark",
			method: "POST",
			data: {},
			dataType: 'json',
			success: function(data) {
				data = data.data
				console.log(data)
				that.remark = data.remark
				that.Bussness = data.Bussness
				that.infor = data.infor
			},

		})
	},
	// 监听页面初次渲染完成
	onReady: function() {
		uni.hideLoading();
		console.log("初始化完毕");
		console.log(this.$data)
	},
	onPageScroll: function(e) {
		if (e.scrollTop >= 200) {
			this.$data.show = true
		} else {
			this.$data.show = false
		}
		if(e.scrollTop>=200&&e.scrollTop<570){
			this.isActives=true;
			this.isActivep=false;
			this.isActivex=false;
		}else if(e.scrollTop>=570&&e.scrollTop<1000){
			this.isActives=false;
			this.isActivep=true;
			this.isActivex=false;
		}else if(e.scrollTop>=1000){
			this.isActives=false;
			this.isActivep=false;
			this.isActivex=true;				
		}
	},
	data() {
		return {
			dotStyle: true,
			towerStart: 0,
			direction: '',
			modalName: null,
			show: false,
			isActives: true,
			isActivep: false,
			isActivex: false,
			remark: {
				classification: [],
				number: '',
				selecterEssays: {
					BuyWhat: '',
					content: '',
					essayname: '',
					issuper: false
				}
			},
			Bussness: {
				name: '',
				image: '',
				where: '',
				service: []
			},
			BaseInfor: [],
			infor: {
				OthersInfo: [],
				Active: [],
				photo: [],
				NewMoney: "",
				OldMoney: "",
				Name: "",
				BottomPhoto: []
			},
			contentHeight: "height:auto",

		}
	},
	methods: {
		GoCar() {
			console.log('现在点击了加入购物车');
		},
		NowBuy() {
			console.log("现在点击了立即购买");
		},
		back() {
			uni.navigateBack({
				delta: 2,
				animationDuration: 200,
				animationType: 'pop-out'
			});
		},
		showModal(e) {
			var that = this
			this.modalName = e.currentTarget.dataset.target
			this.contentHeight = "height:800upx"
			if (this.modalName == "sizeModal") {
				uni.request({
					url: " http://127.0.0.1:3389/BaseInfor",
					method: "POST",
					data: {},
					dataType: 'json',
					success: function(data) {
						that.BaseInfor = data.data.BaseInfo
					}
				})
				this.isActive = true
			} else if (this.modalName == "chooseModal") {
				console.log("chooseModal")
			}
		},
		hideModal(e) {
			this.modalName = null;
			this.contentHeight = "height:auto"
		},
		MenuArrow(e) {
			this.menuArrow = e.detail.value
		},
		showwho(e) {
			this.who = e.target.id;
			if (this.who == 'shangpin') {
				uni.pageScrollTo({
					scrollTop: 0
				})
				this.isActives = true
				this.isActivep = false
				this.isActivex = false

			} else if (this.who == 'pingjia') {
				uni.pageScrollTo({
					scrollTop: 578
				})
				this.isActives = false
				this.isActivep = true
				this.isActivex = false
			} else if (this.who = 'xiangqing') {
				uni.pageScrollTo({
					scrollTop: 1000
				})
				this.isActives = false
				this.isActivep = false
				this.isActivex = true
			}
		},
		moveHandle() {
			return true
		}

	}


}
